import React from 'react';
import $ from 'jquery';
import Layout from '../components/Layout';
import '../assets/scss/intro.scss';
// import Music from '../assets/audio/BamYangGang.mp3';
import { Link } from 'react-router-dom';
import MainJS from './main';


$(document).on('click', '.btn-guide', function(){
	$('#guide').hide();

	//bgm
	if ($('#music_chk').is(':checked')){
		document.getElementById('myAudio').play();
	} else {
		return false;
	}
});

//reload -   position    ...
$(document).on('click', '.btn-reset', function(){
	window.location.reload();
});



const Intro = (props) => {

	$('#three-canvas2').hide();
	$('#three-canvas').show();

	MainJS();
	


	

  return (
		<Layout header>
			<div className='intro'>
				<div id='guide'>
					<div className='guide-text'>
						<p><em>~</em> <br/><b>Click, Drag</b> <br/>!</p>
						<div className='check-group'>
							<input type='checkbox' id='music_chk' />
							<label htmlFor='music_chk'>Play Music</label>
						</div>
						<p className='text-info'>*   (BGM) .</p>
						<button type='button' className='btn-guide'>START</button>
					</div>
				</div>
				<div className='btn-view'>
					{/* <a href='/portfolio-v2/project'>   </a> */}
					<Link to='/Project'>   </Link>
				</div>

				<div className='floating-wrap'>
					<button type='button' className='btn-reset'></button>
				</div>
			</div>
		</Layout>
	);
};


export default Intro;